import React, { useEffect, useState } from 'react';
import { Transition } from 'react-transition-group';
import menuIcon from "../imagens/icons/menu-icon.png";
import closeIcon from "../imagens/icons/close-icon.png"
import logoImg from "../imagens/logo-img.png"

const Header = () => {

    //Controla o menu e sua imagem
    const [showMenu, setShowMenu] = useState(false);
    const [imgMenu, setImgMenu] = useState(menuIcon)
    const nodeRef = React.useRef(null);
    const duration = 200;

    // Animação da barra do menu
    const defaultStyle = {
        right: '-100%',
        transition: `right ${duration * 3}ms ease-in-out`
    };
    const transitionStyles = {
        entering: { right: "0%" },
        entered: { right: "-15%" },
        exiting: { right: "0%" },
        exited: { right: "-100%" }
    };

    // Animação do fundo do menu
    const defaultBackgroundStyle = {
        opacity: "0",
        visibility: "hidden",
        transition: `opacity ${duration * 3}ms ease-in-out, visibility ${duration * 3}ms ease-in-out`
    };
    const transitionBackgroundStyle = {
        entering: { opacity: "0", visibility: "hidden" },
        entered: { opacity: "1", visibility: "visible" },
        exiting: { opacity: "1", visibility: "visible" },
        exited: { opacity: "0", visibility: "hidden" }
    };

    const toggleMenu = () => {
        setShowMenu(!showMenu);
        setImgMenu(!imgMenu)
    };

    //Controla o aumento de tamanho do meu 

    const [isSmall, setIsSmall] = useState(false);

    useEffect(() => {
        let lastScrollTop = 0;

        const handleScroll = () => {
            const currentScrollTop = window.pageYOffset || document.documentElement.scrollTop;
            if (currentScrollTop > lastScrollTop) {
                setIsSmall(true); // Rolando para baixo
            } else {
                setIsSmall(false); // Rolando para cima
            }
            lastScrollTop = currentScrollTop <= 0 ? 0 : currentScrollTop; // Evita valores negativos
        };

        window.addEventListener('scroll', handleScroll);

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    return (
        <div className="header-container">
            <Transition in={showMenu} timeout={0} nodeRef={nodeRef}>
                {(state) => (
                    <div className="menu-container"
                        ref={nodeRef}
                        style={{ ...defaultBackgroundStyle, ...transitionBackgroundStyle[state] }}>
                        <Transition in={showMenu} timeout={0} nodeRef={nodeRef}>
                            {(state) => (
                                <>
                                <button onClick={toggleMenu}></button>
                                <div className="lateral-menu"
                                    ref={nodeRef}
                                    style={{ ...defaultStyle, ...transitionStyles[state] }}
                                >
                                    <ul className="menu-buttons-list-ul">
                                        <div className="" style={{ width: '100%', height: '80px' }}></div>
                                        <li><a href="#simple-list-item-1"><div>Início</div></a></li>
                                        <li><a href="#simple-list-item-2"><div>Benefícios</div></a></li>
                                        <li><a href="#simple-list-item-3"><div>Contratar</div></a></li>
                                    </ul>
                                </div>
                                </>
                            )}
                        </Transition>
                    </div>
                )}
            </Transition>
            <nav className={`navbar navbar-expand-lg navbar-custom ${ isSmall ? 'small' : 'large'}`}>
                <div className="container-fluid">
                    <a href="#simple-list-item-1" className="navbar-brand"><img src={logoImg} alt="logo"></img></a>
                    <button className="navbar-toggler navbar-toggler-custom" type="button" onClick={toggleMenu}>
                        <img src={imgMenu ? menuIcon : closeIcon} alt="menu-img"></img>
                    </button>
                    <div className="collapse navbar-collapse d-none d-lg-flex">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <a className="nav-link active" aria-current="page" href="#simple-list-item-1">Início</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="#simple-list-item-2">Benefícios</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="#simple-list-item-3">Contratar</a>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        </div>

    );



};

export default Header;
