import React from 'react';
import { useForm, ValidationError } from '@formspree/react';


const FormForEmail = () => {

    const [state, handleSubmit] = useForm("mldrdadl");
    if (state.succeeded) {
        return (
            <>
                <div className="container-fluid mt-2 form-container">
                    <h3>Mensagem enviada com sucesso.<br></br>
                    Obrigado!</h3>
                    <h1>&#9786;</h1>
                </div>
            </>
        )
    }



    return (
        <>
            <div id="simple-list-item-3" className="container-fluid mt-5 form-container">
                
                <form onSubmit={handleSubmit}>
                    <div className="mt-4 mb-2">
                        <label for="Full-name" className="form-label" >Nome completo:</label>
                        <input type="text" className="form-control" name="name" id="full-name" placeholder="Primeiro e último nome" required=""></input>
                    </div>
                    <ValidationError
                        prefix="Full-name"
                        field="full-name"
                        errors={state.errors}
                    />

                    <div className="mb-3">
                        <label for="Email" className="form-label">Endereço de E-mail:</label>
                        <input type="email" className="form-control" name="email" id="email" aria-describedby="emailHelp" placeholder="E-mail válido"></input>
                        <div id="emailHelp" className="form-text"></div>
                    </div>
                    <ValidationError
                        prefix="Email"
                        field="email"
                        errors={state.errors}
                    />

                    <div className="mb-2">
                        <label for="Phone-Number" className="form-label">Número de telefone / WhatsApp:</label>
                        <input type="text" className="form-control" name="Número de telefone" id="phone-number" placeholder="Ex: +55 (42)9XXXX-XXXX"></input>
                    </div>
                    <ValidationError
                        prefix="Phone-Number"
                        field="phone-number"
                        errors={state.errors}
                    />

                    <div className="mb-2">
                        <label for="Company-Name" className="form-label">Nome da empresa:</label>
                        <input type="text" className="form-control" name="Nome da empresa" id="company-name" placeholder="Empresa"></input>
                    </div>
                    <ValidationError
                        prefix="Company-Name"
                        field="company-name"
                        errors={state.errors}
                    />

                    <div className="mb-3">
                        <label for="About" className="form-label">Assunto:</label>
                        <input type="text" className="form-control" name="assunto" id="about" placeholder="Sobre o que você quer falar"></input>
                    </div>
                    <ValidationError
                        prefix="About"
                        field="about"
                        errors={state.errors}
                    />



                    <div class="mb-3 form-floating">
                        <textarea className="form-control" placeholder="" id="text-message" name="message"></textarea>
                        <label for="floatingTextarea">Escreva aqui o que você deseja!</label>
                        <ValidationError
                            prefix="Message"
                            field="message"
                            errors={state.errors}
                        />
                    </div>

                    <div className="mb-3">
                        <button type="submit" className="btn btn-success container-fluid" disabled={state.submitting}>
                            Enviar
                        </button>
                    </div>
                </form>

            </div>

        </>
    )
}
export default FormForEmail