import plantImg from "../imagens/plant_img.png";

const Initial = () => {
    return (
        <div className="container-fluid initial-container">
            <div className="initial-images">
                <img className="plant-img" src={plantImg} alt="Imagem de planta" />
            </div>
            <div className="row-cols-3 d-flex flex-column justify-content-center" style={{ height: '100vh' }}>
                <div className="col initial-texts">
                    <h2>
                        Sites profissionais
                        <br />
                        idealizados com criatividade
                    </h2>
                </div>
                <div className="col d-flex justify-content-center initial-link">
                    <a href="#simple-list-item-3">Fazer orçamento</a>
                </div>
                
            </div>
        </div>
    );
}

export default Initial;