
import Frame from "./Frame.js";
import Benefits from "./Benefits";
import TypesWebsites from "./TypesWebsites.js";
import FormForEmail from "./FormForEmail.js";
import Initial from "./Initial.js";


const Body = () => {
  

    return (
        <>
            

            <div className="body-container">

                {/*Parte inicial*/}
                <Initial></Initial>

                {/*Parte onde contem o quadro, textos informativos e o botão de contrato */}
                <Frame></Frame>

                {/*Parte que exemplifica os benefícios de ter um site, e as qualidades de um site meu*/}
                <Benefits></Benefits>

                {/*Parte que demonstra os tipos de sites que eu produzo*/}
                <TypesWebsites></TypesWebsites>

                {/*Parte do formulário que envia para o email alguma mensagem */}
                <FormForEmail></FormForEmail>


            </div>
        </>
    )
}
export default Body