import { useState } from "react";

import ArrowIcon from "../imagens/arrow-down.png";

var projectOrderNumber = 1
const Frame = () => {
    const [firstTextIsCollapsed, setFirstTextIsCollapsed] = useState(true);
    const [secondTextIsCollapsed, setSecondTextIsCollapsed] = useState(false);
    const [threeIsCollapsed, setThreeIsCollapsed] = useState(false);

    //Habilita e desabilita as caixas de texto
    const handleToggleCollapse = (section) => {
        if (section === "first") {
            setFirstTextIsCollapsed(!firstTextIsCollapsed);
            setSecondTextIsCollapsed(false);
            setThreeIsCollapsed(false);
        } else if (section === "second") {
            setSecondTextIsCollapsed(!secondTextIsCollapsed);
            setFirstTextIsCollapsed(false);
            setThreeIsCollapsed(false);
        } else if (section === "three") {
            setThreeIsCollapsed(!threeIsCollapsed);
            setFirstTextIsCollapsed(false);
            setSecondTextIsCollapsed(false);
        }
    };

    //Faz o slide das imagens do projeto

    const [projectStyle, setProjectStyle] = useState("project1");
    const [projectHref, setProjectHref] = useState("https://danillohz.github.io/virtualstore/")
    const [projectName, setProjectName] = useState("Loja Virtual")

    const projects = [
        {
            style: "project1",
            name: "Loja Virtual",
            href: "https://danillohz.github.io/virtualstore/",
            order: 1
        },
        {
            style: "project2",
            name: "Portfólio",
            href: "https://danillohz.github.io/Portfolio/",
            order: 2
        },
        {
            style: "project3",
            name: "Climate",
            href: "https://danillohz.github.io/react-climate/",
            order: 3
        }
    ];

    const changeProject = (direction) => {
        const currentIndex = projects.findIndex(project => project.order === projectOrderNumber);
        let nextIndex = (currentIndex + direction + projects.length) % projects.length;

        const nextProject = projects[nextIndex];
        setProjectStyle(nextProject.style);
        setProjectName(nextProject.name);
        setProjectHref(nextProject.href);
        projectOrderNumber = nextProject.order;
    };

    // Muda para o projeto anterior
    const ChangeProjectLeft = () => {
        changeProject(-1);
    };

    // Muda para o próximo projeto
    const ChangeProjectRight = () => {
        changeProject(1);
    };


    return (

        <div className="portfolio-background">
            <div id="simple-list-item-1" className="Portfolio-container">
            <div className="row">
                <div className="col-md d-flex justify-content-center">
                    <div className="projects-container">
                       

                            <a className={projectStyle} href={projectHref}>
                                {projectName}
                            </a>
                        
                        
                            <button className="change-project-back-btn" onClick={ChangeProjectLeft}><span class="material-symbols-outlined">
                                arrow_back_ios
                            </span></button>
                            <button className="change-project-forward-btn" onClick={ChangeProjectRight}><span className="material-symbols-outlined">
                                arrow_forward_ios
                            </span></button>

                        



                    </div>


                </div>
                <div className="col mt-4 d-flex flex-column">
                    <div className="initial-text container-fluid">
                        <button className="btn button-text mb-3" onClick={() => handleToggleCollapse("first")} type="button" data-bs-toggle="collapse" data-bs-target="#collapseFirstText" aria-expanded={firstTextIsCollapsed} aria-controls="collapseFirstText" >
                            <h2>Você deseja uma obra de arte como essas?</h2>
                            <img src={ArrowIcon} className={firstTextIsCollapsed ? 'rotated' : ''} alt="Arrow"></img>
                        </button>
                        <div className={`collapse ${firstTextIsCollapsed ? "show" : ""}`} id="collapseFirstText">
                            <p className="text-break">Inicialmente devemos ressaltar que para um site chamar a atenção de todos,
                                ele deve ter características modernas e criativas,
                                e para isso, ele deve ser feito por um profissional qualificado,
                                de forma com que o site seja bem estruturado desde seu design até seu conteúdo.<br></br>
                            </p>
                        </div>
                        <button className="btn button-text mb-3" onClick={() => handleToggleCollapse("second")} type="button" data-bs-toggle="collapse" data-bs-target="#collapseSecondText" aria-expanded={secondTextIsCollapsed} aria-controls="collapseSecondText" >
                            <h2>Sobre mim</h2>
                            <img src={ArrowIcon} className={secondTextIsCollapsed ? 'rotated' : ''} alt="Arrow"></img>
                        </button>
                        <div className={`collapse ${secondTextIsCollapsed ? "show" : ""}`} id="collapseSecondText">
                            <p className="text-break">
                                Olá! Meu nome é Danillo Zezepanski e sou um desenvolvedor web e mobile.<br></br>
                                Desde que comecei minha jornada na programação, descobri uma grande satisfação em transformar ideias em realidade através de código.<br></br>
                                Com 2 anos de experiência na área de desenvolvimento, já trabalhei em diversos projetos, desde sites institucionais até aplicativos móveis complexos. Minha experiência abrange tanto desenvolvimento front-end quanto back-end, o que me permite criar soluções completas e integradas.
                            </p>
                        </div>
                    </div>

                    <div className="mt-auto d-flex justify-content-end button-service">
                        <a href="#simple-list-item-3">Contratar Serviço</a>
                    </div>
                </div>
            </div>
        </div>
        </div>
    );
};
export default Frame;
