import React, { useState } from "react";

const Benefits = () => {
  const [isBenefitsActive, setIsBenefitsActive] = useState(true);

  const toggleBenefits = () => {
    setIsBenefitsActive((prevIsBenefitsActive) => !prevIsBenefitsActive);
  };

  const titles = {
    benefits: "Os Benefícios",
    qualities: "O que eu ofereço"
  };

  const cards = {
    benefits: [
      {
        title: "Presença Online 24/7",
        text: "Um site permite que os clientes encontrem informações sobre sua empresa a qualquer hora do dia."
      },
      {
        title: "Credibilidade",
        text: "Um site bem projetado transmite profissionalismo e pode causar uma excelente primeira impressão."
      },
      {
        title: "Alcance Ampliado",
        text: "Um site torna sua empresa acessível a pessoas de todo o mundo."
      },
      {
        title: "Aumento das Vendas",
        text: "Formulários de contato e landing pages ajudam a converter visitantes em leads e clientes."
      }
    ],
    qualities: [
      {
        title: "Design Responsivo",
        text: "Seu site será visualmente atraente em qualquer dispositivo."
      },
      {
        title: "Desempenho Rápido",
        text: "Otimizo para garantir tempos de carregamento rápidos."
      },
      {
        title: "Segurança",
        text: "Implemento práticas de segurança para proteger seus dados."
      },
      {
        title: "Personalização",
        text: "Cada projeto é único e adaptado às suas necessidades."
      }
    ]
  };

  const activeCards = isBenefitsActive ? cards.benefits : cards.qualities;

  return (
    <div id="simple-list-item-2" className="benefits-qualities-container">
      <div className="secondary-title mt-5 d-flex justify-content-center">
        <button type="button" onClick={toggleBenefits}>
          <h2>{isBenefitsActive ? titles.qualities : titles.benefits}</h2>
        </button>
      </div>
      <div className="primary-title">
        <h1>{isBenefitsActive ? titles.benefits : titles.qualities}</h1>

        <div className="row">
          {activeCards.map((card, index) => (
            <div className="col-md d-flex justify-content-center" key={index}>
              <div className="card">
                <div className="card-body">
                  <h5 className="card-title">{card.title}</h5>
                  <p className="card-text">{card.text}</p>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Benefits;
