import React, { useState, useEffect, useRef } from 'react';
import { Transition } from 'react-transition-group';

const Welcome = () => {
    const [floatWelcome, setFloatWelcome] = useState(false);
    const [showWelcome, setShowWelcome] = useState(false);
    const [changeDisplay, setChangeDisplay] = useState('welcome-container');
    const nodeRef = useRef(null);
    const duration = 500;

    // Animação da nuvem de bem vindo
    const defaultStyle = {
        margin: '0px auto',
        transition: `margin ${duration}ms ease-in-out`
    };
    const transitionStyles = {
        entering: { margin: '0px auto' },
        entered: { margin: '70px auto' },
        exiting: { margin: '0px auto' },
        exited: { margin: '80px auto' }
    };

    // Opacidade da nuvem
    const opacityStyle = {
        opacity: '1',
        transition: `opacity ${duration * 3}ms ease-in-out`
    };
    const transitionOpacityStyles = {
        entering: { opacity: '1' },
        entered: { opacity: '0' },
        exiting: { opacity: '0' },
        exited: { opacity: '1' }
    };

    useEffect(() => {

        // Função para bloquear o scroll
        const disableScroll = () => {
            document.body.style.overflow = 'hidden';
        };

        // Função para desbloquear o scroll
        const enableScroll = () => {
            document.body.style.overflow = 'auto';
            document.body.style.overflowX = 'hidden';
        };

        // Bloqueia o scroll
        disableScroll();

        const timers = [
            setTimeout(() => window.scrollTo(0,0), 0),
            setTimeout(() => setFloatWelcome(true), 0),
            setTimeout(() => setFloatWelcome(false), 500),
            setTimeout(() => setFloatWelcome(true), 1000),
            setTimeout(() => setShowWelcome(true), 1200),
            setTimeout(() => {
                setChangeDisplay('welcome-container-none');
                // Desbloqueia o scroll
                enableScroll();
            }, 2500)
        ];

        return () => {
            timers.forEach(timer => clearTimeout(timer));
            // Garante que o scroll seja desbloqueado caso o componente seja desmontado antes do fim da animação
            enableScroll();
        };
    }, []);

    return (
        <>
            <Transition in={showWelcome} timeout={0} nodeRef={nodeRef}>
                {(state) => (
                    <div className={changeDisplay}
                         ref={nodeRef}
                         style={{ ...opacityStyle, ...transitionOpacityStyles[state] }}>
                        <Transition in={floatWelcome} timeout={0} nodeRef={nodeRef}>
                            {(state) => (
                                <div className="welcome-style"
                                     ref={nodeRef}
                                     style={{ ...defaultStyle, ...transitionStyles[state] }}>
                                    <div className="welcome-cloud">
                                        <h1>Seja Bem Vindo</h1>
                                    </div>
                                </div>
                            )}
                        </Transition>
                    </div>
                )}
            </Transition>
        </>
    );
};

export default Welcome;
