import React, { useEffect } from 'react';
import 'aos/dist/aos.css';
import AOS from 'aos';
import landingPageIcon from "../imagens/icons/landing-page-icon.png";
import ecommerceIcon from "../imagens/icons/ecommerce-Icon.png";
import portfolioIcon from "../imagens/icons/portifolio-icon.png";
import contentWebsitesIcon from "../imagens/icons/siteconteudo-icon.png";
import mobileIcon from "../imagens/icons/mobile-icon.png";

const TypesWebsites = () => {
    useEffect(() => {
        // Inicialize os popovers após o carregamento do componente
        const popoverTriggerList = [].slice.call(document.querySelectorAll('[data-bs-toggle="popover"]'));
        popoverTriggerList.map(function (popoverTriggerEl) {
            return new window.bootstrap.Popover(popoverTriggerEl);
        });
    }, []);

    useEffect(() => {
        AOS.init({
            duration: 700, // Duração da animação em milissegundos
            easing: 'ease-in', // Tipo de easing
            once: false, // A animação ocorre apenas uma vez
            offset: 50, // Offset da animação
            delay: 0, // Atraso da animação
            mirror: true, // Anima ao rolar para fora também
        });

        // Atualizar as animações sempre que o componente atualizar
        AOS.refresh();
    }, []);

    return (
        <>
            <h1 className="mt-5 typeswebsites-title">Tipos de Serviço</h1>
            <div className="container-fluid typeswebsites-container">

                <div className="typeswebsites-list">
                    {[
                        { title: "Landing Page", icon: landingPageIcon, alt: "Landing Page", popoverTitle: "Landing Page", popoverText: "Uma landing page é uma página web focada em conversões, criada para campanhas de marketing. Seu objetivo principal é levar o visitante a realizar uma ação específica, como preencher um formulário ou fazer uma compra. Ela possui conteúdo direto, design atraente, uma chamada para ação clara e minimiza distrações para maximizar a taxa de conversão." },
                        { title: "Loja Virtual", icon: ecommerceIcon, alt: "Loja Virtual", popoverTitle: "Loja Virtual", popoverText: "Uma loja virtual é um site onde produtos ou serviços são vendidos online. Ela permite que os clientes naveguem, adicionem itens ao carrinho e façam compras diretamente pela internet, com suporte para pagamento online e segurança nas transações." },
                        { title: "Portfólio", icon: portfolioIcon, alt: "Portfólio", popoverTitle: "Portfólio", popoverText: "Um site de portfólio é uma página web que destaca os projetos, habilidades e experiências de um profissional ou empresa, com o objetivo de impressionar potenciais clientes ou empregadores." },
                        { title: "Conteudista", icon: contentWebsitesIcon, alt: "Conteudista", popoverTitle: "Conteudista", popoverText: "Um site conteudista é uma plataforma online focada na criação e compartilhamento de conteúdo informativo, educativo ou de entretenimento. Seu objetivo é atrair e engajar visitantes através de artigos, vídeos, podcasts ou outros formatos de mídia, geralmente com o intuito de educar, informar ou entreter o público-alvo." },
                        { title: "Mobile App", icon: mobileIcon, alt: "Mobile App", popoverTitle: "Mobile App", popoverText: "Um mobile app é um software projetado para ser usado em dispositivos móveis, como smartphones e tablets, oferecendo diversas funcionalidades e serviços diretamente na palma da mão do usuário." },
                    ].map((service, index) => (
                        <div key={index} className="tipeswebsite-item">

                            <div data-aos="zoom-in-up">
                                <div className="card typeswebsites-card" data-bs-container="body" data-bs-toggle="popover" data-bs-trigger="hover focus" data-bs-placement="bottom" data-bs-title={service.popoverTitle} data-bs-content={service.popoverText}>
                                    <div className="card-body text-center">
                                        <h2 className="card-title">{service.title}</h2>
                                        <img src={service.icon} alt={service.alt} className="img-fluid" />
                                    </div>

                                </div>
                            </div>

                        </div>
                    ))}

                </div>
            </div>

        </>
    );
};

export default TypesWebsites;
